<template>
    <div>
        <el-card>

        <el-tabs v-model="activeName"   @tab-click="clearServer">
            <el-tab-pane label="自定义查询" name="free"></el-tab-pane>
            <el-tab-pane label="procedure" name="procedure"></el-tab-pane>
        </el-tabs>
            <div v-if="activeName == 'free'">
                <el-form   ref="searchForm"  label-width="100px">
                    <el-row :gutter="20">

                        <el-col :span="6">
                            <el-form-item label="服务器">
                                <el-select v-model="curServer" placeholder="请选择服务器" @change="selectCmdList">
                                    <el-option v-for="(i, index) in allServers" :label="i" :value="i"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="6">
                            <el-form-item label="命令">
                                <el-select v-model="curCmd" placeholder="请选择命令" >
                                    <el-option v-for="(i, index) in cmdList" :label="i" :value="i"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="3">
                            <el-button-group>
                                <el-button  type="primary"  @click="addParamShow()">添加参数</el-button>
                                <el-button  type="primary"  @click="query(curCmd, JSON.stringify(Object.fromEntries(param)))" v-if="paramType == 1">搜索</el-button>
                                <el-button  type="primary"  @click="query(curCmd, strParam)" v-if="paramType == 2">搜索</el-button>
                            </el-button-group>
                        </el-col>
                    </el-row>
                    <el-row v-if="paramType == 1" >
                        <el-col  :span="3">
                            请求参数
                        </el-col>
                        <el-col  :span="10">
                            {{JSON.stringify(Object.fromEntries(param))}}
                        </el-col>
                    </el-row>

                    <el-row v-if="paramType == 2" >
                        <el-col  :span="3">
                            请求参数
                        </el-col>
                        <el-col  :span="10">
                            {{strParam}}
                        </el-col>
                    </el-row>




                </el-form>

                <el-dialog title="添加参数" :visible.sync="paramShow" width="50%">
                    <el-button-group>
                        <el-button  type="primary"  @click="paramType = 1">键值编辑</el-button>
                        <el-button  type="primary"  @click="paramType = 2">手动编辑</el-button>
                    </el-button-group>
                    <el-form v-if="paramType == 1">
                    <el-row >
                        <el-col :span="8">
                        <el-form-item label="字段名">
                            <el-input v-model="paramKey" placeholder="请输入字段名"></el-input>
                        </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row >
                        <el-col :span="8">
                        <el-form-item label="字段值">
                            <el-input v-model="paramValue" placeholder="请输入字段值"></el-input>
                        </el-form-item>
                        </el-col>
                    </el-row>
                        <el-button  type="primary"  @click="addParam(paramKey, paramValue)">添加</el-button>
                    </el-form>

                    <template v-for="(value, key ) in param" v-if="paramType == 1">
                        <el-row :gutter="20"  >
                            <el-col :span="5">key：{{value[0]}} </el-col>
                            <el-col :span="5">value：{{value[1]}} </el-col>
                            <el-button  type="mini"  @click="removeParam(value[0])">删除</el-button>
                        </el-row>
                    </template>
                    <el-row v-if="paramType == 2">base demo: abc </el-row>
                    <el-row v-if="paramType == 2">json demo: {"name":"zhangsan","age":14} </el-row>
                    <el-input type="textarea" v-model="strParam" :rows="10" v-if="paramType == 2"></el-input>



                </el-dialog>
                <div v-if="!isBaseType(tableData[0])">
                    <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark">
                        <template v-for="(k, v) in tableData[0]">
                            <el-table-column :prop="v" :label="v" min-width="50"></el-table-column>
                        </template>
                    </el-table>
                </div>
                <div v-if="isBaseType(tableData[0])">
                    <el-row>
                        <el-input type="textarea" :value="JSON.stringify(tableData)" :rows="10"  ></el-input>
                    </el-row>
                </div>

            </div>
            <div v-if="activeName == 'procedure'">
                <el-form   ref="searchForm"  label-width="100px">
                    <el-row :gutter="20">

                        <el-col :span="8">
                            <el-form-item label="服务器">
                                <el-select v-model="curServer" placeholder="请选择服务器" @change="getAllProcedures">
                                    <el-option v-for="(i, index) in allServers" :label="i" :value="i"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <el-col :span="8">
                            <el-form-item label="procedure">
                                <el-select v-model="curProcedure" placeholder="请选择" >
                                    <el-option v-for="(i, index) in allProcedures" :label="i" :value="i"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <el-col :span="3">
                            <el-button-group>
                                <el-button  type="primary"  @click="queryProcedure(curProcedure)">搜索</el-button>
                            </el-button-group>
                        </el-col>
                    </el-row>

                </el-form>

                <el-table ref="multipleTable" :data="procedureLogs" tooltip-effect="dark">
                    <el-table-column prop="result" label="结果" min-width="50"></el-table-column>
                    <el-table-column prop="sum" label="数量" min-width="50"></el-table-column>
                </el-table>
            </div>

        </el-card>

    </div>

</template>

<script>
export default {
    name: "procedure",
    data () {
        return {
            paramShow: false,
            activeName: "free",
            allProcedures: [],
            allServers: [],
            cmdList: [],
            curCmd: "",
            param: new Map(),
            strParam: "",
            paramType: 1,
            procedureLogs:[],
            curServer: "",
            curProcedure: "",
            paramKey: "",
            paramValue: "",
            tableData: [],

        };
    },
    methods:{
        isBaseType(v){
            let type = typeof v;
            if (type === "number" || type === "string" || type === "boolean") {
                return true;
            }
            return false;
        },

        clearServer(){
            this.curServer = "";
            this.procedureLogs = [];
            this.tableData = "";
            this.curProcedure = "";
        },
        addParamShow(){
            this.paramShow = true;

        },
        addParam(key, value){
            if (key == ""){
                return;
            }
            this.param.set(key, value);
            this.$forceUpdate();
            this.paramKey = "";
            this.paramValue = "";
        },
        removeParam(key){
            this.param.delete(key);
            this.$forceUpdate();
        },

        getAllServers(){
            this.$fetch.post(API.log.get_log_servers).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.allServers = res.data;
                } else {
                    this.$message.error(res.desc);
                }
            });
        },

        getAllProcedures(){
            let param = {cmd: "procedure_name_list"}
            let req = {serverName: this.curServer, json: JSON.stringify(param)}
            this.$fetch.post(API.log.query, req).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.allProcedures = JSON.parse(res.data);
                } else {
                    this.$message.error(res.desc);
                }
            });
        },

        selectCmdList(){
            let param = {cmd: "cmd_list"}
            let req = {serverName: this.curServer, json: JSON.stringify(param)}
            this.$fetch.post(API.log.query, req).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.cmdList = JSON.parse(res.data);
                } else {
                    this.$message.error(res.desc);
                }
            });
        },


        queryProcedure(procedureName){
            let param = {cmd: "procedure_results", param: procedureName}
            let req = {serverName: this.curServer, json: JSON.stringify(param)}
            this.$fetch.post(API.log.query, req).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    this.procedureLogs = JSON.parse(res.data);
                } else {
                    this.$message.error(res.desc);
                }
            });
        },

        query(cmd, obj){
            console.log(obj);
            let param = {cmd: cmd, param: obj}
            let req = {serverName: this.curServer, json: JSON.stringify(param)}
            this.$fetch.post(API.log.query, req).then(response => {
                let res = response.data;
                if (res.status == 200) {
                    let r = JSON.parse(res.data);
                    if (r instanceof Array){
                        this.tableData = r;
                    }else {
                        this.tableData = [r];
                    }

                } else {
                    this.$message.error(res.desc);
                }
            });
        },




    },
    mounted() {
        this.getAllServers();
    }

};
</script>


